import createColumnsMenu from './components/columnsMenu';
import getMenu from './components/columnsMenuLegacy';

export interface IConfig {
  locale: {
    insertText2: string;
  }
}

export default function menubar(configs: IConfig) {
  return {
    menu: createColumnsMenu(configs),
    insert2: getMenu(2, configs),
    insert3: getMenu(3, configs),
    insert4: getMenu(4, configs),
    insert5: getMenu(5, configs),
  };
}
